




















































































































































































































@import '~CSS/variables.less';

div.vuix-input {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  outline: none;
  margin: @zero;
  padding: @zero;
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  background-color: inherit;
  color: inherit;
  font-size: @font-size-default;
  border-style: solid;
  border-width: @border-width;
  border-color: @border-top-color @border-right-color @border-bottom-color @border-left-color;
  border-radius: @radius-default;
  transition: @transition-fast border-color, @transition-slow box-shadow;
  height: @height-default;
  width: @width-input-default;
  box-sizing: border-box;
  cursor: text;

  & > div {
    /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
    outline: none;
    margin: @zero;
    border: none;
    border-radius: inherit;
    cursor: inherit;
    padding: @zero;
    background-color: transparent;
    color: inherit;
    font-size: inherit;
    opacity: inherit;
  }

  & > div:first-child {
    /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
    display: inline-flex;
    height: @full;
    min-width: @full;

    & > div {
      /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
      display: flex;
      // min-width: @full;
    }

    & > div > input[data-input] {
      display: block;
      /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
      outline: none;
      margin: @zero;
      border: none;
      border-radius: inherit;
      width: @full;
      min-width: @zero;
      cursor: inherit;
      padding: @padding @font-size-default;
      background-color: transparent;
      color: inherit;
      font-size: inherit;
      opacity: inherit;

      &::-ms-clear {
        display: none;
      }

      &::placeholder {
        color: rgba(@zero, @zero, @zero, @alpha-placeholder);
      }

      &[data-clearable] {
        padding-right: (@padding-default + @clear-size-default);
      }
    }
  }

  & > div[data-clear] {
    /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
    display: inline-flex;
    position: relative;
    right: (@font-size-default + @padding);
    line-height: @zero;

    &:not([data-custom-icon]) {
      opacity: @two-thirds;
    }

    &:not([data-clear-visible]) {
      display: none;
    }

    &:not([data-disabled]):hover {
      cursor: pointer;

      &:not([data-custom-icon]) {
        opacity: @four-fifths;
      }
    }

    &[data-disabled]:hover {
      cursor: default;
    }

    & > div {
      display: block;
      /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
      outline: none;
      margin: @zero;
      border: none;
      border-radius: inherit;
      cursor: inherit;
      padding: @zero;
      background-color: transparent;
      color: inherit;
      font-size: inherit;
      opacity: inherit;
    }
  }

  each(@selectors-size, {
    // noinspection LessUnresolvedVariable
    &.@{value} {
      @size: 'font-size-@{value}';
      @height: 'height-@{value}';
      @width: 'width-input-@{value}';

      height: @@height;
      width: @@width;
      font-size: @@size;

      & > div[data-clear]:not([data-custom-icon]) {
        right: (@@size + @padding);
      }
    }
  });

  each(@selectors-color, {
    // noinspection LessUnresolvedVariable
    &.@{value} > div:first-child {
      & > input[data-input]::placeholder {
        @color: contrast(@@value);
        @red: red(@color);
        @green: green(@color);
        @blue: blue(@color);

        color: rgba(@red, @green, @blue, @alpha-placeholder);
      }
    }
  });
}
