button.vuix-button {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  outline: none;
  margin: 0;
  padding: 0.125rem 1rem;
  border-style: solid;
  border-width: 0.0625rem;
  border-color: #c0c0c0 #9a9a9a #9a9a9a #c0c0c0;
  border-radius: 0.25rem;
  position: relative;
  vertical-align: middle;
  height: 2.375rem;
  background-color: transparent;
  color: inherit;
  font-size: 1rem;
  cursor: pointer;
  transition: 0.1s border-color, 0.2s box-shadow;
}
button.vuix-button::-moz-focus-inner {
  border: none;
}
button.vuix-button.small {
  font-size: 0.75rem;
  height: 1.75rem;
}
button.vuix-button.large {
  font-size: 1.25rem;
  height: 2.75rem;
}
button.vuix-button > span[data-justifiable] {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  outline: none;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  color: inherit;
  cursor: inherit;
  font-size: inherit;
  width: 100%;
  height: 100%;
}
button.vuix-button.left > span[data-justifiable] {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
button.vuix-button.center > span[data-justifiable] {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  display: flex;
  align-items: center;
  justify-content: center;
}
button.vuix-button.right > span[data-justifiable] {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
button.vuix-button.square {
  border-radius: 0;
}
button.vuix-button.circle,
button.vuix-button.square {
  padding: 0;
  height: 2.375rem;
  width: 2.375rem;
}
button.vuix-button.circle > span[data-justifiable],
button.vuix-button.square > span[data-justifiable] {
  border-radius: inherit;
}
button.vuix-button.circle.small,
button.vuix-button.square.small {
  height: 1.75rem;
  width: 1.75rem;
}
button.vuix-button.circle.large,
button.vuix-button.square.large {
  height: 2.75rem;
  width: 2.75rem;
}
