div.vuix-input {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  outline: none;
  margin: 0;
  padding: 0;
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  background-color: inherit;
  color: inherit;
  font-size: 1rem;
  border-style: solid;
  border-width: 0.0625rem;
  border-color: #c0c0c0 #9a9a9a #9a9a9a #c0c0c0;
  border-radius: 0.25rem;
  transition: 0.1s border-color, 0.2s box-shadow;
  height: 2.375rem;
  width: 10rem;
  box-sizing: border-box;
  cursor: text;
}
div.vuix-input > div {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  outline: none;
  margin: 0;
  border: none;
  border-radius: inherit;
  cursor: inherit;
  padding: 0;
  background-color: transparent;
  color: inherit;
  font-size: inherit;
  opacity: inherit;
}
div.vuix-input > div:first-child {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  display: inline-flex;
  height: 100%;
  min-width: 100%;
}
div.vuix-input > div:first-child > div {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  display: flex;
}
div.vuix-input > div:first-child > div > input[data-input] {
  display: block;
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  outline: none;
  margin: 0;
  border: none;
  border-radius: inherit;
  width: 100%;
  min-width: 0;
  cursor: inherit;
  padding: 0.125rem 1rem;
  background-color: transparent;
  color: inherit;
  font-size: inherit;
  opacity: inherit;
}
div.vuix-input > div:first-child > div > input[data-input]::-ms-clear {
  display: none;
}
div.vuix-input > div:first-child > div > input[data-input]::placeholder {
  color: rgba(0, 0, 0, 0.33333333);
}
div.vuix-input > div:first-child > div > input[data-input][data-clearable] {
  padding-right: 2rem;
}
div.vuix-input > div[data-clear] {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  display: inline-flex;
  position: relative;
  right: 1.125rem;
  line-height: 0;
}
div.vuix-input > div[data-clear]:not([data-custom-icon]) {
  opacity: 0.66666667;
}
div.vuix-input > div[data-clear]:not([data-clear-visible]) {
  display: none;
}
div.vuix-input > div[data-clear]:not([data-disabled]):hover {
  cursor: pointer;
}
div.vuix-input > div[data-clear]:not([data-disabled]):hover:not([data-custom-icon]) {
  opacity: 0.8;
}
div.vuix-input > div[data-clear][data-disabled]:hover {
  cursor: default;
}
div.vuix-input > div[data-clear] > div {
  display: block;
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  outline: none;
  margin: 0;
  border: none;
  border-radius: inherit;
  cursor: inherit;
  padding: 0;
  background-color: transparent;
  color: inherit;
  font-size: inherit;
  opacity: inherit;
}
div.vuix-input.small {
  height: 1.75rem;
  width: 7.5rem;
  font-size: 0.75rem;
}
div.vuix-input.small > div[data-clear]:not([data-custom-icon]) {
  right: 0.875rem;
}
div.vuix-input.large {
  height: 2.75rem;
  width: 12.5rem;
  font-size: 1.25rem;
}
div.vuix-input.large > div[data-clear]:not([data-custom-icon]) {
  right: 1.375rem;
}
div.vuix-input.primary > div:first-child > input[data-input]::placeholder {
  color: rgba(255, 255, 255, 0.33333333);
}
div.vuix-input.secondary > div:first-child > input[data-input]::placeholder {
  color: rgba(255, 255, 255, 0.33333333);
}
div.vuix-input.info > div:first-child > input[data-input]::placeholder {
  color: rgba(255, 255, 255, 0.33333333);
}
div.vuix-input.success > div:first-child > input[data-input]::placeholder {
  color: rgba(255, 255, 255, 0.33333333);
}
div.vuix-input.warning > div:first-child > input[data-input]::placeholder {
  color: rgba(0, 0, 0, 0.33333333);
}
div.vuix-input.error > div:first-child > input[data-input]::placeholder {
  color: rgba(255, 255, 255, 0.33333333);
}
div.vuix-input.dark > div:first-child > input[data-input]::placeholder {
  color: rgba(255, 255, 255, 0.33333333);
}
div.vuix-input.light > div:first-child > input[data-input]::placeholder {
  color: rgba(0, 0, 0, 0.33333333);
}
