label.vuix-label {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  display: inline-flex;
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  outline: none;
  margin: 0;
  border: 0.0625rem none;
  border-color: #c0c0c0 #9a9a9a #9a9a9a #c0c0c0;
  border-radius: 0.25rem;
  font-size: 1rem;
  background-color: inherit;
  color: inherit;
}
label.vuix-label.right-angled {
  border-radius: 0;
}
label.vuix-label.solid {
  border-style: solid;
}
label.vuix-label.dotted {
  border-style: dotted;
}
label.vuix-label.dashed {
  border-style: dashed;
}
label.vuix-label.primary {
  background-color: #007bff;
}
label.vuix-label.secondary {
  background-color: #6c757d;
}
label.vuix-label.info {
  background-color: #17a2b8;
}
label.vuix-label.success {
  background-color: #28a745;
}
label.vuix-label.warning {
  background-color: #ffc107;
}
label.vuix-label.error {
  background-color: #dc3544;
}
label.vuix-label.dark {
  background-color: #343a40;
}
label.vuix-label.light {
  background-color: #f8f9fa;
}
