.space-before::before {
  content: ' ';
  white-space: pre;
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  display: inline-flex;
}
.space-after::after {
  content: ' ';
  white-space: pre;
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  display: inline-flex;
}
.none {
  display: none !important;
}
.hidden {
  visibility: hidden !important;
}
.vuix[data-shapeable].round {
  border-radius: 0.25rem;
}
.vuix[data-shapeable].pill {
  border-radius: 1000000000rem;
}
.vuix[data-shapeable].circle {
  border-radius: 50%;
}
.vuix[data-shapeable].right-angled {
  border-radius: 0;
}
.vuix[data-borderable].solid {
  border-style: solid;
}
.vuix[data-borderable].dotted {
  border-style: dotted;
}
.vuix[data-borderable].dashed {
  border-style: dashed;
}
.vuix[data-borderable].borderless {
  border: none;
}
.vuix[data-colorable].primary {
  background-color: #007bff;
  color: #ffffff;
}
.vuix[data-colorable].secondary {
  background-color: #6c757d;
  color: #ffffff;
}
.vuix[data-colorable].info {
  background-color: #17a2b8;
  color: #ffffff;
}
.vuix[data-colorable].success {
  background-color: #28a745;
  color: #ffffff;
}
.vuix[data-colorable].warning {
  background-color: #ffc107;
  color: #000000;
}
.vuix[data-colorable].error {
  background-color: #dc3544;
  color: #ffffff;
}
.vuix[data-colorable].dark {
  background-color: #343a40;
  color: #ffffff;
}
.vuix[data-colorable].light {
  background-color: #f8f9fa;
  color: #000000;
}
.vuix[data-blockable].block,
.vuix.vuix-input[data-blockable].block {
  /* stylelint-disable-next-line plugin/no-unsupported-browser-features */
  display: flex;
  width: 100%;
}
.vuix[data-focused]:not(:disabled):not([data-disabled]),
.vuix[data-focusable]:not(:disabled):not([data-disabled]):focus {
  box-shadow: 0 0 0 0.125rem #00bfff;
}
.vuix[data-hoverable]:not(:disabled):not([data-disabled]):hover {
  box-shadow: 0 0 0 0.125rem #0000cd;
}
.vuix[data-active]:not(:disabled):not([data-disabled]),
.vuix[data-activatable]:not(:disabled):not([data-disabled]):active {
  box-shadow: 0 0 0 0.125rem #006dd9;
}
.vuix[data-active]:not(:disabled):not([data-disabled]):not(.borderless),
.vuix[data-activatable]:not(:disabled):not([data-disabled]):active:not(.borderless) {
  border-color: #9a9a9a #c0c0c0 #c0c0c0 #9a9a9a;
}
.vuix[data-active]:not(:disabled):not([data-disabled]) > *,
.vuix[data-activatable]:not(:disabled):not([data-disabled]):active > * {
  position: relative;
}
.vuix[data-active]:not(:disabled):not([data-disabled]).primary,
.vuix[data-activatable]:not(:disabled):not([data-disabled]):active.primary {
  background-color: #0074f0;
}
.vuix[data-active]:not(:disabled):not([data-disabled]).secondary,
.vuix[data-activatable]:not(:disabled):not([data-disabled]):active.secondary {
  background-color: #656d75;
}
.vuix[data-active]:not(:disabled):not([data-disabled]).info,
.vuix[data-activatable]:not(:disabled):not([data-disabled]):active.info {
  background-color: #1596aa;
}
.vuix[data-active]:not(:disabled):not([data-disabled]).success,
.vuix[data-activatable]:not(:disabled):not([data-disabled]):active.success {
  background-color: #259b40;
}
.vuix[data-active]:not(:disabled):not([data-disabled]).warning,
.vuix[data-activatable]:not(:disabled):not([data-disabled]):active.warning {
  background-color: #f7b900;
}
.vuix[data-active]:not(:disabled):not([data-disabled]).error,
.vuix[data-activatable]:not(:disabled):not([data-disabled]):active.error {
  background-color: #da2838;
}
.vuix[data-active]:not(:disabled):not([data-disabled]).dark,
.vuix[data-activatable]:not(:disabled):not([data-disabled]):active.dark {
  background-color: #2d3238;
}
.vuix[data-active]:not(:disabled):not([data-disabled]).light,
.vuix[data-activatable]:not(:disabled):not([data-disabled]):active.light {
  background-color: #eff1f4;
}
.vuix[data-disabled]:not([data-wrapper-only]) {
  cursor: default;
}
.vuix[data-disabled]:not([data-wrapper-only]):not([data-cross]) {
  opacity: 0.5;
}
